@use "styles/sassVariables" as v;
@use "styles/stylesFunctions.scss" as stylesFunctions;

.body {
  background-color: #f8f8f8;
  padding-top: 98px;
  padding-bottom: 208px;
}

.pageHeaders {
  h3,
  p {
    margin: 0;
    text-align: center;
    width: 100%;
  }
  h3 {
    @include stylesFunctions.fontStyles(70px, 52px, 600, #000000, #0000);
  }
  p {
    @include stylesFunctions.fontStyles(35px, 26px, 300, #000000, #0000);
    opacity: 0.5;
    margin-top: 20px;
  }
}

.Form {
  @include stylesFunctions.flexLayout(center, flex-start, column, 20px);
  h3,
  label,
  span,
  p {
    margin: 0;
  }
  h3 {
    @include stylesFunctions.fontStyles(45px, 34px, 600, #000000, #0000);
  }
  label {
    width: 100%;
    div {
      margin-top: 9px;
      @include stylesFunctions.flexLayout(space-between, center, row, initial);
      a {
        text-decoration: underline;
        color: #ffaa03;
      }
    }
  }

  input[type="number"],
  input[type="date"] {
    padding: 22px 31px;
    width: 100%;
    border-radius: 5px;
    border: solid 0.5px #d2d2d2;
    @include stylesFunctions.fontStyles(39px, 29px, 300, #000000, #0000);
  }

  textarea {
    width: 100%;
    height: 150px;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    background-color: #d2d2d2;
    font-size: 20px;
    resize: none;
    @include stylesFunctions.rtl(dir, ltr, initial);
    @include stylesFunctions.rtl(dir, initial, rtl);
  }

  input[type="submit"] {
    background-color: #001736;
    border-radius: 5px;
    width: 100%;
    padding-top: 22px;
    padding-bottom: 22px;
    @include stylesFunctions.fontStyles(29px, 22px, 600, #ffffff, #0000);
  }
}
